<script setup lang="ts">
import usePagination from "~/composables/use-pagination";
import { ORDER, SORT } from "~/constants";
import TicketAPI from "~/services/ppv-ticket";
import getErrorLocalePathMessage from "~/utils/locale";
import { getPriceLabel } from "~/utils/subscription";

export interface ListPPVTicketsProps {
  limit?: number;
  hasLoadMoreButton?: boolean;
  hasLazyPagination?: boolean;
}
const props = withDefaults(defineProps<ListPPVTicketsProps>(), {
  limit: 5,
});

const { t, te } = useI18n();

const { items: userPPVTickets, loading, canLoadMore, loadMore, reset, error } = usePagination<IModelCommerce>({
  pageSize: props.limit,
  immediate: true,
  loadItemsFn: (page: number, pageSize: number) => TicketAPI.load({ page, pageSize, sort: SORT.added, order: ORDER.desc }),
});
const errorMessage = computed(() => error.value && getErrorLocalePathMessage(error.value, { t, te }).message);
</script>

<template>
  <BaseList
    grid-class="form-column"
    root-class="!p-0"
    :limit="props.limit"
    :items="userPPVTickets"
    :loading="loading"
    :error="errorMessage"
    :has-load-more-button="props.hasLoadMoreButton && canLoadMore && Boolean(userPPVTickets.length)"
    :has-lazy-pagination="props.hasLazyPagination && canLoadMore"
    @click-load-more="loadMore()"
    @visible="reset()"
  >
    <template #item="{ item }">
      <BaseButton
        :key="item.id"
        :name="`item-ppv-${item.id}`"
        :to="`/account/ppv/${item.id}/`"
        class="block w-full"
        tag="nuxt-link"
        type="icon"
      >
        <CardPPV
          class="mb-6"
          :title="item.title"
          :price="getPriceLabel(item.price, item.currency)"
        />
      </BaseButton>
    </template>
    <template #skeleton>
      <CardPPVSkeleton />
    </template>
    <template #empty>
      <div class="flex flex-col items-center mt-6 gap-4">
        <IconTicket fill="var(--base-icon-color)" />
        <div class="text-base text-center text-secondary whitespace-pre-wrap">
          {{ t('pages.account.tab_ppv_no_ppv') }}
        </div>
      </div>
    </template>
  </BaseList>
</template>
